var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"about-icon"},[_c('about-icon__headline',[_c('headline',{attrs:{"position":"center","size":"36px","h":"2"},scopedSlots:_vm._u([{key:"first",fn:function(){return [_vm._v("Об иконе: ")]},proxy:true},{key:"second",fn:function(){return [_vm._v("история")]},proxy:true}])})],1),_c('div',{staticClass:"flex"},[_c('about-icon__image'),_c('about-icon__content',[_c('about-icon__paragraph',[_c('paragraph',[_vm._v("\n                    Одним из немаловажных вопросов изучения \n                    древнерусского зодчества все еще остается \n                    вопрос о сложении архитектурных композиций \n                    древнейших городов Руси, вопрос выявления \n                    основных закономерностей этого живого \n                    динамичного процесса, а также поэтапной \n                    характеристики самих городских ансамблей \n                    от их зарождения до заключительного периода \n                    их развития, приходящегося, как правило, \n                    на вторую половину XVII столетия.\n                ")])],1),_c('about-icon__paragraph',[_c('paragraph',[_vm._v("\n                    Икона позволяет составить более полное \n                    представление о характере городского \n                    ансамбля Пскова в последней четверти \n                    XVII века. Она была задумано художником \n                    как своеобразный реально-исторический \n                    фон, на котором развертываются легендарные \n                    события весьма популярного в средневековом \n                    Пскове \"Сказания о явлении Божией матери \n                    кузнецу Дорофею\" во время осады Пскова \n                    польским королем Стефаном Баторием в 1581 \n                    году.\n                ")])],1),_c('about-icon__paragraph',[_c('paragraph',[_vm._v("\n                    Икона Жиглевича была ранее упомянута Н. \n                    Окулич-Казариным в его известной книге о \n                    Пскове; тогда же была сделана и попытка \n                    ее воспроизведения, однако весьма неудачная \n                    в типографском отношении. При этом никакой \n                    расшифровки изображенных на иконе зданий \n                    сделано не было. За этой иконой утвердилось \n                    условное название \"икона Жиглевича\", так как \n                    некогда она находилась в псковском Гостином \n                    дворе, возле лавки купца И. И. Жиглевича. \n                    Размер иконы 68 * 60 см.\n                ")])],1),_c('about-icon__paragraph',[_c('paragraph',[_vm._v("\n                    О времени написания иконы можно сказать \n                    следующее. В настоящем своем виде ее \n                    живописная поверхность может быть отнесена \n                    лишь к первой половине XIX века. \n                    Окулич-Казарин указывал (ссылаясь на \n                    слова очевидцев) на надпись на оборотной \n                    стороне иконы о поновлении ее в 1838 \n                    году. Возникает вопрос: представляет \n                    ли собой \"икона Жиглевича\" лишь точную \n                    копию с более древнего утраченного \n                    памятника или все же является, хотя \n                    и поновленным (то есть прописанным в \n                    XIX веке), но оригиналом конца XVII \n                    века? Данные чисто технологического \n                    порядка (характер обработки иконной \n                    доски и тип грунта) и в особенности \n                    сама манера живописи указывают на то, \n                    что памятник является позднейшей копией с \n                    несохранившейся иконы конца XVII века и \n                    что под упомянутым Окулич-Казариным поновлением \n                    иконы в 1838 году следует понимать или \n                    же написание в это время новой иконы по \n                    достаточно точно снятому с древнего \n                    оригинала \"переводу\" (прориси) его \n                    графической композиции, или же поновление \n                    (пропись) \"списка\" с памятника XVII века, \n                    но \"списка\" все равно позднейшего - \n                    вероятно, не древнее середины XVIII века.\n                ")])],1),_c('about-icon__paragraph',[_c('paragraph',[_vm._v("\n                    О достаточной, по-видимому, адекватности \n                    изображений зданий на \"иконе Жиглевича\" \n                    изображениям их на не дошедшем до нас \n                    оригинале (то есть о графической точности \n                    чертежа) позволяет судить тот факт, что \n                    представленные на иконе элементы городского \n                    ансамбля не носят более или менее явных \n                    следов архитектурно-графических искажений, \n                    обычно возникающих в связи с каким-либо \n                    непониманием позднейшими поновителями \n                    первоначального рисунка. Показанные \n                    здания, их характер согласуются с \n                    изображениями зданий на плане Пскова \n                    1694 года, на Владычней иконе, а также \n                    на плане 1740 года и могут быть со \n                    значительной степенью достоверности \n                    идентифицированы с реальными городскими \n                    постройками.\n                ")])],1),_c('about-icon__paragraph',[_c('paragraph',[_vm._v("\n                    Протооригинал \"иконы Жиглевича\" отражает \n                    топографическую ситуацию Пскова 1686 - \n                    1689 годов и был создан примерно в этот \n                    же период. Несмотря на известную условность \n                    всех имеющихся на публикуемом плане \n                    изображений, они, в общем, дают верную \n                    картину как всего городского ансамбля в \n                    целом, так и отдельных городских сооружений \n                    (например, у некоторых храмов показано \n                    наличие шатровых колоколен, появившихся \n                    лишь в XVII веке, показаны типичные \n                    псковские звонницы, наличие определенных \n                    приделов и притворов). Данная икона \n                    представляет интерес не только с точки \n                    зрения истории топографии древнего Пскова, \n                    но любопытен и тем, что дополнительно \n                    знакомит нас, хотя бы и в схематических \n                    чертах, с рядом несохранившихся древних \n                    культовых сооружений города. Иконный \n                    чертеж знакомит и с характером ансамблей \n                    Мирожского и Псково-Печерского монастырей \n                    на заключительном этапе их средневекового \n                    развития.\n                ")])],1)],1)],1)],1)}
